<template>
  <div class="navhear">
    <div class="left" @click="goBack"><slot name="left"><i class="iconfont icon-fanhui"></i></slot></div>
    <div class="center"><slot></slot></div>
    <div class="right"><slot name="right"></slot></div>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'
export default {
  name:'TopNav',
  setup(){

    const router = useRouter()
    //返回上一个界面
    const goBack = () => {
      router.go(-1)
    }
    return{
      goBack
    }
  }
}
</script>

